<template>
  <div class="el-table-wrapper" :class="wrapperClass"
       v-loading="data.loading"
       :element-loading-text="loadingText"
       :element-loading-spinner="loadingIcon"
       :element-loading-background="loadingColor"
  >
    <div class="el-table-wrapper-header" :style="headerStyle" v-if="headerVisible">
      <div class="el-table-wrapper-header-text">
        <slot name="header" :data="slotData">
          <template v-if="isMultipleSelection">
            <template v-if="selectionData.cache.length">
               {{selectionData.cache.length}} 
            </template>
            <template v-else></template>
          </template>
        </slot>
      </div>
      <pager position="top" v-show="!pDisabled && pagerPosition !== 'bottom'">
        <slot name="pagerPrepend" :data="data" slot="pagerPrepend" slot-scope="{data}"/>
        <slot name="pagerSummary" :data="data" slot="pagerSummary" slot-scope="{data}"/>
        <slot name="pagerAppend" :data="data" slot="pagerAppend" slot-scope="{data}"/>
      </pager>
    </div>
    <div class="el-table-wrapper-content" :style="contentStyle">
      <div class="el-table-wrapper-title-toolbar" v-if="tShowHeader">
        <slot name="titleToolbar" :data="slotData"/>
      </div>
      <el-table
        style="width: 100%"
        height="100%"
        :highlight-current-row="tHighlightCurrentRow || selection === 'single'"
        :data="currentData"
        @current-change="onTableCurrentRowChanged"
        @selection-change="onTableSelectionChanged"
        :maxHeight="tMaxHeight"
        :stripe="tStripe"
        :border="tBorder"
        :size="tSize"
        :fit="tFit"
        :show-header="tShowHeader"
        :current-row-key="tCurrentRowKey"
        :row-class-name="tRowClassName"
        :row-style="tRowStyle"
        :cell-class-name="tCellClassName"
        :cell-style="tCellStyle"
        :header-row-class-name="tHeaderRowClassName"
        :header-row-style="tHeaderRowStyle"
        :header-cell-class-name="tHeaderCellClassName"
        :header-cell-style="tHeaderCellStyle"
        :row-key="tRowKey || getDataId"
        :empty-text="tEmptyText"
        :default-expand-all="tDefaultExpandAll"
        :expand-row-keys="tExpandRowKeys"
        :default-sort="tDefaultSort"
        :tooltip-effect="tTooltipEffect"
        :show-summary="tShowSummary"
        :sum-text="tSumText"
        :summary-method="tSummaryMethod"
        :span-method="tSpanMethod"
        :select-on-indeterminate="tSelectOnIndeterminate"
        :indent="tIndent"
        :lazy="tLazy"
        :load="tLoad"
        @cell-mouse-enter="onCellMouseEnter"
        @cell-mouse-leave="onCellMouseLeave"
        @cell-click="onCellClick"
        @cell-dblclick="onCellDblclick"
        @row-click="onRowClick"
        @row-contextmenu="onRowContextmenu"
        @row-dblclick="onRowDblclick"
        @header-click="onHeaderClick"
        @header-contextmenu="onHeaderContextmenu"
        @sort-change="onSortChange"
        @filter-change="onFilterChange"
        @header-dragend="onHeaderDragend"
        @expand-change="onExpandChange"
        ref="table"
      >
        <slot/>
        <slot name="empty" slot="empty">
          {{tEmptyText}}
        </slot>
        <slot name="append" slot="append"/>
      </el-table>
    </div>
    <div class="el-table-wrapper-footer" :style="footerStyle" v-if="footerVisible">
      <div class="el-table-wrapper-footer-text">
        <slot name="footer" :data="slotData">
          <template v-if="isMultipleSelection">
            <template v-if="selectionData.cache.length">
               {{selectionData.cache.length}} 
            </template>
            <template v-else></template>
          </template>
        </slot>
      </div>
      <pager position="bottom" v-show="!pDisabled && pagerPosition !== 'top'">
        <slot name="pagerPrepend" :data="data" slot="pagerPrepend" slot-scope="{data}"/>
        <slot name="pagerSummary" :data="data" slot="pagerSummary" slot-scope="{data}"/>
        <slot name="pagerAppend" :data="data" slot="pagerAppend" slot-scope="{data}"/>
      </pager>
    </div>
  </div>
</template>

<script>
import Pager from './Pager'
import './index.less'
import publicMethods from './publicMethods'
import privateMethods from './privateMethods'
import handlers from './handlers'
import data from './data'
import computed from './computed'
import watch from './watch'

export default {
  name: 'ElTableWrapper',
  mixins: [computed, data, handlers, privateMethods, publicMethods, watch],
  components: {Pager},
  provide() {
    return {
      elTableRoot: this
    }
  },
  props: {
    //  BEGIN===========================================================
    /**
     * : l(local), i(increment), s(server)
     */
    type: {
      type: String,
      default: 's'
    },
    /**
     * 
     * 
     */
    idField: {
      type: [String, Array, Function],
      default: 'id'
    },
    /**
     * 
     */
    autoHeight: {
      type: Boolean,
      default: false
    },
    /**
     *  true 
     * 
     *  idField 
     */
    advanceSelection: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    customClass: {
      type: String
    },
    /**
     *  bottom top both
     */
    pagerPosition: {
      type: String,
      default: 'bottom'
    },
    headerSize: {
      type: [String, Number],
      default: 48
    },
    footerSize: {
      type: [String, Number],
      default: 40
    },
    showFooter: {
      type: Boolean,
      default: true
    },
    /**
     * id
     */
    defaultId: {
      type: [String, Number],
      default: ''
    },
    /**
     * : , single, multiple type="selection"  multiple
     */
    selection: {
      type: String
    },
    /**
     * 
     */
    value: {
    },
    /**
     * 
     */
    toggleOnRowClick: {
      type: Boolean,
      default: false
    },
    checkField: {
      type: String
    },
    //  END===========================================================

    // AJAX BEGIN===========================================================
    /**
     *  Promise 
     */
    ajax: {
      type: Function
    },
    /**
     * url
     */
    url: {
      type: String
    },
    /**
     * method
     */
    method: {
      type: String,
      default: 'get'
    },
    /**
     * ajax
     */
    ajaxDelay: {
      type: Number,
      default: 500
    },
    /**
     * 
     */
    params: {
      type: Object
    },
    responseHandler: {
      type: Function
    },
    /**
     * 
     */
    autoLoad: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    paramIndex: {
      type: String,
      default: 'pageIndex'
    },
    /**
     * 
     */
    paramSize: {
      type: String,
      default: 'pageSize'
    },
    /**
     * 
     */
    totalField: {
      type: String,
      default: 'total'
    },
    /**
     * 
     */
    listField: {
      type: String,
      default: 'list'
    },
    /**
     * aja
     */
    checkParams: {
      type: Function
    },
    /**
     * ajax
     */
    loadWhenParamsChange: {
      type: Boolean,
      default: false
    },
    // AJAX END===========================================================

    //  BEGIN======================================================
    /**
     * 
     */
    pageSize: {
      type: [String, Number],
      default: 10
    },
    /**
     * 
     */
    index: {
      type: [String, Number],
      default: 1
    },
    /**
     * 
     */
    showPagerSummary: {
      type: Boolean,
      default: true
    },
    //  END======================================================

    //  BEGIN======================================================
    /**
     * 
     */
    localData: {
      type: Array
    },
    //  END======================================================
    //  BEGIN======================================================
    /**
     * 
     */
    incSize: {
      type: [String, Number],
      default: 80
    },
    /**
     * 
     */
    paramInc: {
      type: String,
      default: 'lastId'
    },
    /**
     *  idField 
     *  idField 
     */
    incId: {
      type: [String, Array, Function]
    },
    //  END======================================================
    // Loading BEGIN======================================================
    loading: {
      type: Boolean,
      default: false
    },
    loadingText: {
      type: String,
      default: 'Loading...'
    },
    loadingIcon: {
      type: String,
      default: 'el-icon-loading'
    },
    loadingColor: {
      type: String,
      default: 'rgba(249, 249, 249, 0.9)'
    },
    // Loading END======================================================

    // ElTable  BEGIN =======================================
    tMaxHeight: {
      type: [String, Number]
    },
    tStripe: {
      type: Boolean,
      default: false
    },
    tBorder: {
      type: Boolean,
      default: false
    },
    tSize: {
      type: String
    },
    tFit: {
      type: Boolean,
      default: true
    },
    tShowHeader: {
      type: Boolean,
      default: true
    },
    tCurrentRowKey: {
      type: [String, Number]
    },
    tRowClassName: {
      type: [String, Function]
    },
    tRowStyle: {
      type: [String, Function]
    },
    tCellClassName: {
      type: [String, Function]
    },
    tCellStyle: {
      type: [String, Function]
    },
    tHeaderRowClassName: {
      type: [String, Function]
    },
    tHeaderRowStyle: {
      type: [String, Function]
    },
    tHeaderCellClassName: {
      type: [String, Function]
    },
    tHeaderCellStyle: {
      type: [String, Function]
    },
    tRowKey: {
      type: [String, Function]
    },
    tEmptyText: {
      type: String,
      default: ''
    },
    tDefaultExpandAll: {
      type: Boolean,
      default: false
    },
    tExpandRowKeys: {
      type: Array
    },
    tDefaultSort: {
      type: Object,
      default: () => {
        return {
          order: 'ascending'
        }
      }
    },
    tTooltipEffect: {
      type: String
    },
    tShowSummary: {
      type: Boolean,
      default: false
    },
    tSumText: {
      type: String,
      default: ''
    },
    tSummaryMethod: {
      type: Function
    },
    tSpanMethod: {
      type: Function
    },
    tHighlightCurrentRow: {
      type: Boolean,
      default: false
    },
    tSelectOnIndeterminate: {
      type: Boolean,
      default: true
    },
    tIndent: {
      type: Number,
      default: 16
    },
    tLazy: {
      type: Boolean
    },
    tLoad: {
      type: Function
    },
    // ElTable  END =======================================
    // ELPagination  BEGIN ================================
    pSmall: {
      type: Boolean,
      default: false
    },
    pBackground: {
      type: Boolean,
      default: false
    },
    pPagerCount: {
      type: Number,
      default: 7
    },
    pLayout: {
      type: String,
      default: 'prev, pager, next, jumper, ->, total'
    },
    pPageSizes: {
      type: Array,
      default: () => {
        return [10, 20, 30, 40, 50, 100]
      }
    },
    pPopperClass: {
      type: String
    },
    pPrevText: {
      type: String
    },
    pNextText: {
      type: String
    },
    pDisabled: {
      type: Boolean,
      default: false
    },
    pHideOnSinglePage: {
      type: Boolean
    }
    // ELPagination  END ==================================
  },
  mounted() {
    this.__init()
    this._checkProps()
    if (this.type === 'l') {
      this._updateLocalData(this.localData)
    } else if (this.autoLoad) {
      this._loadRemoteData()
    }
  }
}
</script>
