/**
 * 
 */

export default {
  methods: {
    /**
     * 
     * @param clear ( clear )
     * @return {methods}
     */
    load(clear = true) {
      clearTimeout(this._ajaxHandle)
      this._ajaxHandle = setTimeout(() => {
        this._loadRemoteData(() => {
          if (clear) {
            this.clear()
          }
          this.resetScroll()
        }, clear ? this.defaultId : undefined)
      }, this.ajaxDelay)
      return this
    },
    /**
     * ajax
     */
    cancel() {
      clearTimeout(this._ajaxHandle)
      return this
    },
    /**
     * 
     */
    clear() {
      this.data.cache = []
      this.data.view = []
      this.data.extra = null
      this.data.count = 0
      this.pager.index = 1
      this.pager.count = 0
      this.selectionData.cache = []
      this.selectionData.all = {}
      this.selectionData.current = {}
      this.$emit('data-size-change', 0)
      return this
    },
    /**
     * 
     * @return {{pageIndex: number,pageCount: number,pageSize: number,dataSize: number,selected: number}}
     */
    info() {
      return {
        pageIndex: this.pager.index,
        pageCount: this.pager.count,
        pageSize: this.pager.size,
        dataSize: this.data.count,
        selected: this.selectionData.cache.length
      }
    },
    /**
     * 
     * @param row
     */
    append(row) {
      if (Array.isArray(row)) {
        this.data.cache = this.data.cache.concat(row)
      } else {
        this.data.cache.push(row)
      }
      this.data.count = this.data.cache.length
      this.pager.count = Math.ceil(this.data.count / this.pager.size)
      this.data.view = this.data.cache
      return this
    },
    /**
     * 
     * @param row
     */
    prepend(row) {
      if (Array.isArray(row)) {
        this.data.cache = row.concat(this.data.cache)
      } else {
        this.data.cache.unshift(row)
      }
      this.data.count = this.data.cache.length
      this.pager.count = Math.ceil(this.data.count / this.pager.size)
      this.data.view = this.data.cache
      return this
    },
    /**
     * 
     * @param row 
     * @param index
     */
    insert(row, index) {
      if (Array.isArray(row)) {
        [].splice.apply(this.data.cache, [index, 0].concat(row))
      } else {
        this.data.cache.splice(index, 0, row)
      }
      this.data.count = this.data.cache.length
      this.pager.count = Math.ceil(this.data.count / this.pager.size)
      this.data.view = this.data.cache
      return this
    },
    /**
     * 
     * @param rows
     */
    remove(rows) {
      if (!Array.isArray(rows)) {
        rows = [rows]
      }
      rows.forEach(row => {
        let id = this.getDataId(row)
        let idx = this.data.cache.findIndex(row => this.getDataId(row) === id)
        if (idx >= 0) {
          this.data.cache.splice(idx, 1)
        }
        idx = this.selectionData.cache.findIndex(row => this.getDataId(row) === id)
        if (idx >= 0) {
          this.selectionData.cache.splice(idx, 1)
        }
      })
      this.data.count = this.data.cache.length
      this.pager.count = Math.ceil(this.data.count / this.pager.size)
      this.data.view = this.data.cache
      return this
    },
    /**
     * 
     * @param rows
     */
    update(rows) {
      if (!Array.isArray(rows)) {
        rows = [rows]
      }
      let temp = {}
      rows.forEach(row => {
        temp[this.getDataId(row)] = row
      })

      let remain = rows.length
      for (let i = 0; i < this.data.cache.length; i++) {
        let row = this.data.cache[i]
        let id = this.getDataId(row)
        if (!temp.hasOwnProperty(id)) {
          continue
        }
        this.$set(this.data.cache, i, temp[id])
        delete temp[id]
        remain--
        if (remain <= 0) {
          break
        }
      }
      return this
    },
    /**
     *  idField  id
     * @param row
     * @param idField
     * @return {String}
     */
    getDataId(row, idField) {
      if (row === undefined || row === null) {
        return undefined
      }

      //  id
      if (typeof row === 'string' || typeof row === 'number') {
        return row.toString()
      }

      idField = idField || this.idField
      if (typeof idField === 'function') {
        return idField(row).toString()
      }
      if (!Array.isArray(idField)) {
        if (!row.hasOwnProperty(idField)) {
          this._throwError(`Field "${idField}" not found in data row, a valid "id-field" property is expected`)
        }
        return row[idField].toString()
      }
      let temp = row
      idField.forEach(field => {
        if (!temp.hasOwnProperty(field)) {
          this._throwError(`Field "${idField.join('.')}" not found in data row, a valid "id-field" property is expected`)
        }
        temp = temp[field].toString()
      })
      return temp
    },
    /**
     * 
     * @param rows
     */
    select(rows) {
      let all = this.selectionData.all
      let current = this.selectionData.current
      let cache = this.selectionData.cache
      // 
      if (!this.isMultipleSelection) {
        let id = this.getDataId(rows)
        this.selectionData.all = this.selectionData.current = {
          [id]: rows
        }
        this._updateSelection()
        return
      }
      // 
      if (!Array.isArray(rows)) {
        rows = [rows]
      }
      rows.forEach(row => {
        let id = this.getDataId(row)
        if (!all.hasOwnProperty(id)) {
          all[id] = row
          current[id] = row
          cache.push(row)
        }
        if (this.checkField) {
          row[this.checkField] = true
        }
      })
      this._updateSelection()
      return this
    },
    /**
     * 
     * @return {[]} 
     */
    selectAll() {
      if (!this.isMultipleSelection) {
        console.warn('ElTableWrapper: method "selectAll" only allowed for multiple selectionData')
        return []
      }
      let data = this.advanceSelection ? this.data.cache : this.currentData
      this.select(data)
      return data
    },
    /**
     *
     * @param rows 
     */
    deselect(rows) {
      if (!this.isMultipleSelection) {
        this.selectionData.all = this.selectionData.current = {}
        this.selectionData.cache = []
        this._updateSelection()
        return
      }
      if (!Array.isArray(rows)) {
        rows = [rows]
      }
      let all = this.selectionData.all
      let current = this.selectionData.current
      let cache = this.selectionData.cache
      this.selectionData.ignore = true
      rows.forEach(row => {
        let id = this.getDataId(row)
        if (all.hasOwnProperty(id)) {
          delete all[id]
          delete current[id]
          let idx = cache.findIndex(row => this.getDataId(row) === id)
          if (idx >= 0) {
            cache.splice(idx, 1)
          }
        }
        this.$refs.table.toggleRowSelection(row, false)
        if (this.checkField) {
          row[this.checkField] = false
        }
      })
      this.selectionData.ignore = false
      return this
    },
    /**
     * 
     * @return {[]} 
     */
    deselectAll() {
      if (!this.isMultipleSelection) {
        console.warn('ElTableWrapper: method "deselectAll" only allowed for multiple selection')
        return []
      }
      let data = this.advanceSelection ? this.data.cache : this.currentData
      this.deselect(data)
      return data
    },
    /**
     * 
     * @param rows
     */
    toggle(rows) {
      if (!this.isMultipleSelection) {
        console.warn('ElTableWrapper: method "toggle" only allowed for multiple selection')
        return this
      }

      if (!rows) {
        return this
      }

      if (!Array.isArray(rows)) {
        rows = [rows]
      }

      // 
      const cache = this.selectionData.cache
      const current = this.selectionData.current
      const all = this.selectionData.all

      // 
      let removeRows = []

      // 
      const newRows = []

      this.selectionData.ignore = true
      rows.forEach(row => {
        this.$refs.table.toggleRowSelection(row)
        const id = this.getDataId(row)
        if (this.selectionData.all.hasOwnProperty(id)) {
          // 
          delete current[id]
          delete all[id]

          const idx = cache.findIndex(row => this.getDataId(row) === id)
          if (idx >= 0) {
            cache.splice(idx, 1)
          }
          removeRows.push(row)
        } else {
          // 
          current[id] = row
          all[id] = row
          cache.push(row)
          newRows.push(row)
        }
      })
      this.selectionData.ignore = false

      if (removeRows.length) {
        this.$emit('selection-change', {
          selection: [].concat(this.selectionData.cache),
          type: 'deselect',
          changed: removeRows,
          allSelected: false
        })
      }

      if (newRows.length) {
        this.$emit('selection-change', {
          selection: [].concat(this.selectionData.cache),
          type: 'select',
          changed: removeRows,
          allSelected: cache.length > 0 && cache.length === this.data.cache.length
        })
      }

      return this
    },
    /**
     * 
     * @return {*[]|*}
     */
    getSelection() {
      if (this.isMultipleSelection) {
        return [].concat(this.selectionData.cache)
      }
      return this.selectionData.cache[0]
    },
    /**
     * 
     */
    clearSelection() {
      if (!this.selectionData.cache.length) {
        return this
      }
      if (this.isMultipleSelection) {
        this.$refs.table.clearSelection()
      } else {
        this.$refs.table.setCurrentRow()
      }
      return this
    },
    clearSort() {
      this.$refs.table.clearSort()
      return this
    },
    clearFilter(columnKey) {
      this.$refs.table.clearFilter(columnKey)
      return this
    },
    doLayout() {
      this.$refs.table.doLayout()
      return this
    },
    sort(prop, order) {
      this.$refs.table.sort(prop, order)
      return this
    }
  }
}
